import React, { useState, useEffect } from "react";
import axios from "axios";
import { useDispatch } from "react-redux";
import { Form, Button, Col, Container, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import Header from "./Header";
import Config from "../../config/Config.json";
import classes from "./Register.module.css"; // Adjust the path as needed

const Login = () => {
  const [inputs, setInputs] = useState({ email: "", password: "" });
  const [errors, setErrors] = useState({});
  const [backendErrors, setBackendErrors] = useState({
    show: false,
    message: "",
  });
  const dispatch = useDispatch();

  useEffect(() => {
    document.title = Config.TITLE.LOGIN;
  }, []);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setInputs((prevInputs) => ({ ...prevInputs, [name]: value }));
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (validate()) {
      setBackendErrors({ show: false, message: "" });
      try {
        const res = await axios.post("https://ucjobs-zone.onrender.com/auth/login", inputs);
        const token = res.data.token;
        dispatch({
          type: "SETAUTHTOKEN",
          data: token,
        });
      } catch (err) {
        const statusCode = err.response?.status || err.message.split(" ").pop();
        const errorMessage = statusCode === "401" || statusCode === "422"
          ? "Incorrect Email or Password"
          : "Some error...on our side...";
        setBackendErrors({
          show: true,
          message: errorMessage,
        });
      }
    }
  };

  const validate = () => {
    let isValid = true;
    const error = {};

    if (!inputs.email) {
      isValid = false;
      error.email = "Please enter your email address.";
    } else if (!/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/i.test(inputs.email)) {
      isValid = false;
      error.email = "Please enter a valid email address.";
    }

    if (!inputs.password) {
      isValid = false;
      error.password = "Please enter your password.";
    } else if (inputs.password.length < 6) {
      isValid = false;
      error.password = "Password must be at least 6 characters.";
    }

    setErrors(error);
    return isValid;
  };

  return (
    <React.Fragment>
      <Header />
      <Container className="mb-5 mt-4">
        <Row className="mb-5">
          <Col
            lg={5}
            md={6}
            sm={8}
            className={`${classes.formContainer} p-5 m-auto shadow-sm rounded-lg`}
          >
            <h2 className="text-center mb-4" tyle={{ color: "#2c49ed" }}>
              Welcome Back!
            </h2>
            {backendErrors.show && (
              <div className="login-error text-danger">{backendErrors.message}</div>
            )}
            <Form onSubmit={handleSubmit}>
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Email <span className="text-danger">*</span></Form.Label>
                <Form.Control
                  type="email"
                  name="email"
                  placeholder="Enter email"
                  value={inputs.email}
                  onChange={handleChange}
                />
                {errors.email && <p className="text-danger">{errors.email}</p>}
              </Form.Group>

              <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label>Password <span className="text-danger">*</span></Form.Label>
                <Form.Control
                  type="password"
                  name="password"
                  placeholder="Password"
                  value={inputs.password}
                  onChange={handleChange}
                />
                {errors.password && <p className="text-danger">{errors.password}</p>}
              </Form.Group>

              <div className="d-grid gap-2">
                <Button variant="primary" size="lg" type="submit" className="mt-4 mb-2">
                  Log-In
                </Button>
              </div>

              <Col lg={12} className="text-center mt-3">
                <Link to="/Reset" className="text-decoration-none">
                  Forgot Password?
                </Link>
              </Col>

              <Row className="mt-4 text-center">
                <Col>
                  <Form.Label>Not an existing user?</Form.Label>
                </Col>
                <Col>
                  <Link to="/Register">
                    <Button variant="success" size="lg" className="mt-4 w-100">
                      Sign Up Now
                    </Button>
                  </Link>
                </Col>
              </Row>
            </Form>
          </Col>
        </Row>
      </Container>
    </React.Fragment>
  );
};

export default Login;
